main {
   .spin-wrapper{
    width: 100%;
    position: absolute;
    height: 100%;
    z-index: 8;
    top: 0;
    left: 0;
    overflow: hidden;
    min-width: 350px;
    background-color:#a8a5a4;
    
  .spinner{
    position: absolute;
    height: 60px;
    width: 60px;
    border: 3px solid transparent;
    border-top-color: #A04668;
    top: 50%;
    left: 50%;
    margin: -30px;
    border-radius: 50%;
    animation: spin 2s linear infinite;
    
    &:before, &:after{
      content:'';
      position: absolute;
      border: 3px solid transparent;
      border-radius: 50%;
    }
    
    &:before{
      border-top-color: #254E70;
      top: -12px;
      left: -12px;
      right: -12px;
      bottom: -12px;
      animation: spin 3s linear infinite;
    }
    
    &:after{
      border-top-color: #FFFBFE;
      top: 6px;
      left: 6px;
      right: 6px;
      bottom: 6px;  
      animation: spin 4s linear infinite;
    }
  }
   }
    .blog-home {
        min-height: 100vh;
        display: flex;
        position: relative;
        overflow: hidden;

        .menu-btn {
            position: absolute;
            right: 20px;
            top: 20px;
            cursor: pointer;
            color: #FF3644;
            font-size: 40px;
            z-index: 5;
            transition: transform .3s;

            &:hover {
                transform: scale(1.1);
            }
        }

        @media only screen and (max-width:1040px) {
            padding-top: 35px;
            flex-direction: column;
            gap: 15px;
        }

        .slider-reg {
            z-index: 5;
            bottom: 0;
            left: 20px;
            position: absolute;

            .btn-right,
            .btn-left {
                margin-right: 10px;
                font-size: 40px;
                color: #FF3644;
                transition: color .3s;
                cursor: pointer;
                transform: rotate(180deg);

                &:hover {
                    color: white;
                }
            }
        }

        .blog-left {
            width: 40%;
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;


            @media only screen and (max-width:1040px) {
                width: 100%;
                flex-direction: row;
                align-items: flex-start;
                justify-content: space-around;
            }

            @media only screen and (max-width:670px) {
                align-items: flex-end;
            }

            .logo {
                width: 65%;

                @media only screen and (max-width:1150px) {
                    width: 80%;
                }

                @media only screen and (max-width:1040px) {
                    width: 200px;
                }

                @media only screen and (max-width:670px) {
                    width: 100px;
                }

                ;
            }


            .div {
                width: 384px;
                height: 100%;
                position: absolute;
                top: 61%;
                right: 20px;
                overflow: hidden;
                gap: 50px;

                @media only screen and (max-width:1040px) {
                    position: static;
                    width: 50%;
                }

                @media only screen and (max-width:670px) {
                    display: inline;
                    height: 112px;
                }
            }

            .blog-left-info {
                width: 100%;
                display: flex;
                flex-direction: column;
                gap: 10px;
                align-items: flex-end;
                position: absolute;
                opacity: 0;
                transition: transform 700ms linear, opacity 700ms ease;

                @media only screen and (max-width:1040px) {
                    width: 50%;
                }

                @media only screen and (max-width:670px) {}

                &.active {
                    transform: translateY(0);
                    opacity: 1;
                    z-index: 1;
                }

                &.next {
                    transform: translateY(100%);
                }

                &.last {
                    transform: translateY(-100%);
                }

                h1 {
                    width: 100%;
                    letter-spacing: 3px;
                    font-size: 50px;
                }

                h2 {
                    @media only screen and (max-width:670px) {
                        display: none;
                    }
                }

                p {
                    font-size: 19px;
                    animation: animP 800ms ease;

                    @media only screen and (max-width:670px) {
                        display: none;
                    }
                }

                button {
                    width: 100px;
                    height: 35px;
                    border: none;
                    letter-spacing: 1px;
                    font-family: 'Courier New', Courier, monospace;
                    font-size: 15px;
                    font-weight: 600;
                    cursor: pointer;
                    color: white;
                    background: #FF3644;
                    transition: background-color 500ms ease, color 500ms ease, border 500ms ease;

                    &:hover {
                        background-color: white;
                        color: #FF3644;
                        border: 2px solid #FF3644;
                    }


                }
            }
        }

        .blog-right {
            display: flex;
            align-items: center;
            width: 60%;
            overflow: hidden;
            position: relative;

            @media only screen and (max-width:1040px) {
                height: 76vh;
                width: 100%;
            }

            @media only screen and (max-width:670px) {
                height: 88vh;
            }

            @media only screen and (max-width:300px) {
                height: 100vh;
            }

            .slider-img {
                width: 100%;
                height: 100%;
                opacity: 0;
                overflow: hidden;
                object-fit: cover;
                position: absolute;
                top: 0;
                left: 0;
                transition: transform 700ms linear,opacity 700ms linear;

                &.active {
                    transform: translateX(0);
                    opacity: 1;
                }

                &.next {
                    transform: translateX(100%);
                }

                &.last {
                    transform: translateX(-100%);
                }

            }
        }

    }
}


@keyframes animP {
    from {
        opacity: 0;
        transform: translateY(-100%);
    }
}


@keyframes spin{
    0% {transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
  }